class ButtonsWeekRecord extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
			isNotPress : true,
			prevDay:"ПН",
			activeDay: "ПН"
		};
		this.handleClickOnButton = this.handleClickOnButton.bind(this);
	}
	componentDidUpdate(){
		console.log(this.state.activeDay);
		var prevElement = document.getElementById(this.state.prevDay);
		prevElement.style.opacity = "1.0";
		prevElement.style.backgroundColor = "#FFFFFF";
		prevElement.style.color = "#4672C4";
		var element = document.getElementById(this.state.activeDay);
		element.style.opacity = "0.6";
		element.style.backgroundColor = "#4672C4";
		element.style.color = "#FFFFFF";
	}

	handleClickOnButton(e, day){
		this.setState({
			prevDay: this.state.activeDay,
			activeDay : day
		});
		this.props.changeOfDay(day);
	}
	render() {
		return (
			<Div className= "RecordBlock">
				<ButtonDayRecord day = "ПН" changeOfDay = {(e) => this.handleClickOnButton(e, "ПН")}/>
				<ButtonDayRecord day = "ВТ" changeOfDay = {(e) => this.handleClickOnButton(e, "ВТ")}/>
				<ButtonDayRecord day = "СР" changeOfDay = {(e) => this.handleClickOnButton(e, "СР")}/>
				<ButtonDayRecord day = "ЧТ" changeOfDay = {(e) => this.handleClickOnButton(e, "ЧТ")}/>
				<ButtonDayRecord day = "ПТ" changeOfDay = {(e) => this.handleClickOnButton(e, "ПТ")}/>
				<ButtonDayRecord day = "СБ" changeOfDay = {(e) => this.handleClickOnButton(e, "СБ")}/>
				<ButtonDayRecord day = "ВС" changeOfDay = {(e) => this.handleClickOnButton(e, "ВС")}/>
			</Div>
		);
	}
}


function ButtonDayRecord(props){
	return(
		<Button id = {props.day} className="ButtonMothsDay" size="l" mode="outline" onClick = {props.changeOfDay}>
			<div className="FontClass">{props.day}</div>
		</Button>
	)
}

function GetDayCount(data, day){
	if (data != null && data.has(day)){
		return data.get(day);
	}
	return null;
}

function GetCount(data, time){
	if (data != null && data.has(time)){
		return data.get(time);
	}
	return 4;
}

function RecordSelection(props){
	var count = GetCount(props.data, props.time);
	return (
		<Button id ={props.time} className = "ButtonDataTime"  size="l" mode="outline" onClick = {(e) => props.confirm(e.currentTarget.dataset.to, count)} data-to= {props.time}>
			<Div className = "TimeRecord">{props.time}</Div>
			<Div className = "CountRecord">{count}</Div>
		</Button>
	)
}

class ButtonsTimeRecord extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
			isNotPress : this.props.isPress,
			prevTime: "14:00",
			activeTime: "14:00"
		};
		this.handleClickOnButton = this.handleClickOnButton.bind(this);
	}
	componentDidUpdate(){
		if(this.state.isNotPress){
			console.log(this.state.activeTime);
			var prevElement = document.getElementById(this.state.prevTime);
			prevElement.style.opacity = "1.0";
			var element = document.getElementById(this.state.activeTime);
			element.style.opacity = "0.4";
		}
	}

	handleClickOnButton(time, count){
		this.setState({
			prevTime: this.state.activeTime,
			activeTime : time,
			isNotPress: true
		});
		this.props.confirm(time, count);
	}
	render() {
	var data = GetDayCount(this.props.data, this.props.day);
	return(
		<Div className="RecordBlockDataTime" id = {this.props.day}>
			<RecordSelection time =  "14:00" confirm = {this.handleClickOnButton} data = {data}/>
			<RecordSelection time ="16:00" confirm = {this.handleClickOnButton} data = {data}/>
			<RecordSelection time ="18:00" confirm = {this.handleClickOnButton} data = {data}/>
			<RecordSelection time ="20:00" confirm = {this.handleClickOnButton} data = {data}/>
			<RecordSelection time ="22:00" confirm = {this.handleClickOnButton} data = {data}/>
		</Div>
	);
	}
}


function ButtonsForDay(props){
	var data = GetDayCount(props.data, props.day);
	return(
		<Div className="RecordBlockDataTime" id = {props.day}>
			<RecordSelection  time ={props.day} confirm = {props.confirm} data = {data}/>
			<RecordSelection time ="16:00" confirm = {props.confirm} data = {data}/>
			<RecordSelection time ="18:00" confirm = {props.confirm} data = {data}/>
			<RecordSelection time ="20:00" confirm = {props.confirm} data = {data}/>
			<RecordSelection time ="22:00" confirm = {props.confirm} data = {data}/>
		</Div>
	)
}

class ActiveButtons extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		  day : "ПН",
		  isConfirm : false,
		  countWashMachine :0,
		  time : "",
		  countFree : 0,
		  data : this.props.data,
		  isPress : false,
		  isComplete : false
		};
	  this.handleClick = this.handleClick.bind(this);
	  this.handleClickOnDataTime = this.handleClickOnDataTime.bind(this);
	  this.handleClickOnCountWashMashines = this.handleClickOnCountWashMashines.bind(this);
	  this.handleClickOnDataTimeEvent = this.handleClickOnDataTimeEvent.bind(this);
	  this.handleClickOnSignUp = this.handleClickOnSignUp.bind(this);
	  this.handleClickOnConfirmRecord = this.handleClickOnConfirmRecord.bind(this);
	  this.handleClickOnComplete = this.handleClickOnComplete.bind(this);
	}
	handleClickOnComplete(){
		this.setState({
			isComplete : !this.state.isComplete
		})
	}
	handleClick(newDay) {
		this.setState(
			{
				day: newDay
		});
	  }
	handleClickOnDataTime(){
		this.setState(state =>(
			{
				isConfirm : !state.isConfirm,
		}));
	}
	handleClickOnDataTimeEvent(newTime, currentCount){
		this.setState(state =>(
			{
				time : newTime,
				countFree :  currentCount
		}));
		console.log(currentCount);
	}
	handleClickOnCountWashMashines(count){
		console.log(this.state.day);
		console.log(count);
		console.log(this.state.countFree);
		var free =  this.state.countFree;
		if (free < count){
			console.log(0)
			this.setState(
				{
					countWashMachine: 0
			});
		}
		else {
			console.log(count);
			this.setState(
			{
				countWashMachine: count
			});
		}
	}
	async handleClickOnSignUp(){
		var newData = await this.props.update();
		this.setState(
			{
				data : newData,
				isConfirm : !this.state.isConfirm,
		});
	}
	async handleClickOnConfirmRecord(){
		var user = await bridge.send('VKWebAppGetUserInfo');
		await RecordOnWashing(this.state.countFree,this.state.day, this.state.time, this.state.countWashMachine, user.id );
		await this.handleClickOnSignUp();
	}

	render() {
		var alertComplete = this.state.isComplete ? <AlertRecordComplete close = {this.handleClickOnComplete}/> : null;
		var alertConfirm = this.state.isConfirm ? 
		<AlertConfirm complete = {this.handleClickOnComplete} activeCount = {this.state.countFree} confirm = {() => this.handleClickOnConfirmRecord()} close = {() => this.handleClickOnDataTime()} changeCount = {this.handleClickOnCountWashMashines} countFree = {this.state.countFree}/>: null;
		return (
			<Div>
				{alertComplete}
				{alertConfirm}
				<ButtonsWeekRecord changeOfDay = {this.handleClick} />
				<ButtonsTimeRecord isPress = {this.state.isPress} day = {this.state.day} confirm = {this.handleClickOnDataTimeEvent} data = {this.state.data}/>
				<Div className = "ButtonBlocksRecord">
					<Button className="ButtonBlcokRecord" size="xl" mode="outline" onClick = {() => this.handleClickOnSignUp()}>
						<Div className = "FontButtonRecord">ЗАПИСАТЬСЯ</Div>
					</Button>
					<Button className="ButtonBlcokRecord" size="xl" mode="outline" onClick={this.props.go} data-to="mainMenu">
						<Div className = "FontButtonRecord">НАЗАД</Div>
					</Button>
				</Div>
			</Div>
		);
	}
}

