class AlertConfirm extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			activeButton : null
		};
		this.handleClickOnButton = this.handleClickOnButton.bind(this);
		this.handleClickOnConfirm = this.handleClickOnConfirm.bind(this);
	}
	componentDidMount(){
		console.log("HDSd");
		RenderNotConfirm(this.props.activeCount);
		RenderButtonConfirm(this.state.activeButton, "confirm");
	}
	componentDidUpdate(){
		RenderButtonConfirm(this.state.activeButton, "confirm");
		for(var i = 1; i <= 4; i++){
			var id = i+"but";
			var element = document.getElementById(id);
			if (this.state.activeButton == id){
				element.style.opacity = "0.6";
				element.style.backgroundColor = "#4672C4";
				element.style.color = "#FFFFFF";
			}
			else{
				element.style.opacity = "1.0";
				element.style.backgroundColor = "#FFFFFF";
				element.style.color = "#4672C4";
			}
		}
	}
	handleClickOnButton(count){
		if(isNotPressAlertConfirm(this.props.activeCount, count)){
			return;
		}
		this.props.changeCount(count);
		this.setState({
			prevButton: this.state.activeButton,
			activeButton : count+"but"
		});
	}
	async handleClickOnConfirm(){
		if(RenderButtonConfirm(this.state.activeButton, "confirm")){
			await this.props.confirm();
			this.props.complete();
		}
	}
	render(){
		return(
			<Alert actionsLayout = "vertical" onClose = {this.props.close} >
			<Div className = "TextNotify">Выберите нужное количество машинок</Div>
			<Div className = "ButtonsCountWashingMachines">
				<Button id = "1but" className = "ButtonWashMachine"  size="l" mode="outline" >1</Button>
				<Button id = "2but" className = "ButtonWashMachine"  size="l" mode="outline" >2</Button>
				<Button id = "3but" className = "ButtonWashMachine"  size="l" mode="outline" >3</Button>
				<Button id = "4but" className = "ButtonWashMachine"  size="l" mode="outline" >4</Button>
			</Div>
			<Button id = "confirm" className = "ConfirmRecord" size="xl" mode="outline" onClick = {this.handleClickOnConfirm}>Потвердить</Button>
		  </Alert>
		)
	}
}

function RenderButtonConfirm(activeButton,id){
	var element =  document.getElementById(id);
	if(activeButton == null){
		element.style.borderColor = "#FF0000";
		return false;
	}
	element.style.borderColor = "#4672C4";
	return true;
}

function isNotPressAlertConfirm(activeCount, count){
	return activeCount < count;
}


