async function AddRecord(newDay, newTime, newCount, newUser, currentCount){
	var urlRecord = "https://e2185f7223d7.ngrok.io/record";
	let response = await fetch(urlRecord, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({
			day: newDay,
			time: newTime,
			countFree: newCount,
			usersNumber: newUser,
			countBusy : currentCount
		}),  // типа запрашиаемого документа
    	headers: new Headers({
      	'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
		let answer = await response.text();
		console.log(response.text);
		return answer;
	} 
	return "NOCONNECT"; 
}

async function UpdateRecord(updateDay, updateTime, updateCount, updateUser, currentCount){
	var urlRecord = "https://e2185f7223d7.ngrok.io/record";
	let response = await fetch(urlRecord, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'PUT',              // метод POST
    	body: JSON.stringify({
			day: updateDay,
			time: updateTime,
			countFree: updateCount,
			usersNumber: updateUser,
			countBusy : currentCount
		}),  // типа запрашиаемого документа
    	headers: new Headers({
      	'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
		let answer = await response.text();
		console.log(response.text);
		return answer;
	} 
	return "NOCONNECT"; 
}


async function RecordOnWashing(currentCountFree, day, time, countWashMach, user){
	if(currentCountFree == 4){
		await AddRecord(day, time, 4 - countWashMach, user, countWashMach);
		console.log("ADD");
	}
	else{
		await UpdateRecord(day, time, currentCountFree - countWashMach, user, countWashMach);
		console.log("UPDATE");
	}
}

const RecordMenu = props => (
	<Panel id={props.id}>
		<PanelHeader>Laundry</PanelHeader>
		<Div className="MainRecord">
			<Div className= "TextBlockRecord">
				Выберите время, дату и количество машинок
			</Div>
			<ActiveButtons goBack = {props.goBack} update = {props.getRecord} data = {props.dataRecord} go = {props.go}/>
		</Div>
	</Panel>
);

RecordMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RecordMenu;
