const urlDataBaseUsers = "https://e2185f7223d7.ngrok.io/databaseUsers";

async function IsAuthorization(userId){
	let response = await fetch(urlDataBaseUsers, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({idVk: userId}),  // типа запрашиаемого документа
    	headers: new Headers({
      		'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
  		let data = await response.json();
		if (data.length > 0){
			console.log(data);
			return true;
		}
		console.log("NO Data");
		return false;
	} 
	console.log("No connect");
	return false;
}

var urlRecord = "https://e2185f7223d7.ngrok.io/record";

async function GetRecord(){
	var dict = new Map();
	let response = await fetch(urlRecord);
	let records = await response.json(); // читаем ответ в формате JSON
	for(var i=0; i < records.length; i++){
		if(!dict.has(records[i].day)){
			var secondDict = new Map();
			secondDict.set(records[i].time, records[i].countFree);
			dict.set(records[i].day, secondDict);
		}
		else{
			dict.get(records[i].day).set(records[i].time, records[i].countFree)
		}
	}
	console.log("Я работаю");
	return dict;
}

async function GetRecordUser(){
	var url  = "https://e2185f7223d7.ngrok.io/recordUser";
	var dict = new Map();
	var user = await bridge.send('VKWebAppGetUserInfo');
	let response = await fetch(url, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({id: user.id}),  // типа запрашиаемого документа
    	headers: new Headers({
      	'Content-Type': 'application/json'
    	}),
	});
	if(response.ok){
		let records = await response.json(); // читаем ответ в формате JSON
		for(var i=0; i < records.length; i++){
			if(!dict.has(records[i].day)){
				var secondDict = new Map();
				secondDict.set(records[i].time, records[i].countBusy);
				dict.set(records[i].day, secondDict);
			}
			else{
				dict.get(records[i].day).set(records[i].time, records[i].countBusy)
			}
		}
		console.log("Я работаю");
		return dict;
	}
	return null;
}



const App = () => {
	const [activePanel, setActivePanel] = useState('startMenu');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [dataRecord, setDataRecord] = useState(null);
	const [dataUser, setDataUser] = useState(null);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	async function goStart(){
		let user = await bridge.send('VKWebAppGetUserInfo');
		console.log(user.id);
		var condition = await IsAuthorization(user.id);
		if (condition){
			await getDataUser();
			setActivePanel('mainMenu');
			return;
		}
		await getDataUser();
		setActivePanel('registrationMenu');
	}

	async function goRecord(){
		let data = await GetRecord();
		console.log(data);
		setDataRecord(data);
		setActivePanel('recordMenu');
	}

	async function getDataUser(){
		var data = await GetRecordUser();
		setDataUser(data);
	}

	async function goBackToMain(){
		await getDataUser();
		setActivePanel("mainMenu");
	}

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<StartMenu id ='startMenu' go={go} />
			<CodeMenu id ='codeMenu' go={go} />
			<MainMenu id ='mainMenu' getDataUser = {getDataUser} go={go} data = {dataUser} fetchedUser={fetchedUser} goRecord = {goRecord}/>
			<RecordMenu id ='recordMenu' goBack = {goBackToMain} go={go} dataRecord = {dataRecord} getRecord = {GetRecord}/>
			<RegistrationMenu id ='registrationMenu' go={go} fetchedUser={fetchedUser}/>
		</View>
	);
}

export default App;

