import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import bridge from '@vkontakte/vk-bridge';

import Welcome from '../img/Welcome.png';
import './StartMenuStyle.css';

const osName = platform();

const StartMenu = props => (
	<Panel id={props.id}>
		<PanelHeader>
		 	Laundry
		</PanelHeader>
		<Div className = "Main">
			<Div className = "TextBlock">
				Добро пожаловать в Laundry!
			</Div>
			<Div className = "MainBlock">
			<Div className = "ImgWelcomeBlock">
				<img className = "ImgWelcome" src={Welcome}></img>
				<Div className = "TextFirstBlock">
					Это приложение, специально создано 
					для студентов УрФУ, проживающих в общежитии.
				</Div>
				<Div className = "TextFirstBlock">C нашей помощью вы сможете легко записаться
				в прачечную, постираться и не забыть о предстоящей стирке!
				</Div>
			</Div>
			<Div className = "ButtonPosition">
				<Button className = "ButtonBlock" size="xl" mode="outline" onClick={props.go} data-to="registrationMenu">
					<Div className = "FontButtonStart">НАЧАТЬ!</Div>
				</Button>
			</Div>
			</Div>
		</Div>
	</Panel>
);
//{<AuthorizationPage userId = {props.fetchedUser.id}

StartMenu .propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default StartMenu ;
