import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import { PopoutWrapper } from '@vkontakte/vkui';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import App from '../App.js';

import Coderegistration from '../img/Coderegistration.png';
import './RegistrationMenuStyle.css';

const osName = platform();

var notification = null;
var keyDefault = 0;

const request = new XMLHttpRequest();
var urlRegUsers = "https://e2185f7223d7.ngrok.io/databaseNumberReg";

async function Registration(userId){
	let enteredNumber = document.getElementById("input-id").value;
	let response = await fetch(urlRegUsers, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({NumberRegistration: enteredNumber, idVk: userId}),  // типа запрашиаемого документа
    	headers: new Headers({
      		'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
  		let date = await response.text();
  		console.log(response.text);
  		if (date === "NO"){
			console.log("NO");
  			renderingNotifyIsOff();
  		}
  		else{
  			showHide("Hide");
  			console.log("Find");
  		}
	} 
	else {
  		console.log(response.status);	
  }
};


async function NotifyIsOff(){
	keyDefault = document.getElementById("input-id").value;
	let response = await fetch(urlRegUsers, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({key: keyDefault}),  // типа запрашиаемого документа
    	headers: new Headers({
      	'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
  		let date = await response.json();
  		console.log(response.text);
  		if (date.length < 1){
  			renderingNotifyIsOff();
  		}
  		else{
  			showHide("Hide");
  			console.log("Find");
  		}
	} 
	else {
  		console.log(response.status);	
  }
};


function closePopout(){
	notification = null;
}

function renderingNotifyIsOff(){
	notification = <Alert actionsLayout = "vertical" onClose = {closePopout} >
		<Div className = "TextNotify">Неверный код. Пожалуйста, попробуйте еще раз</Div>
      </Alert>
};

class RenderNotify extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    	popout: null,
    	actionsLog: [],
    };
  }

  componentDidMount() {
	setInterval(() => {
   		this.setState({popout: notification})    
  		}, 100);
  }
  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  render() {
    return (
      <div>
		{this.state.popout}
      </div>
    );
  }
}

function showHide(element) {
	if (document.getElementById(element)) { 
    	var obj = document.getElementById(element); 
    	var obj2 = document.getElementById("NotHide");
    	if (obj.style.display != "block") { 
        	obj.style.display = "block";
        	obj2.style.display = "none"; //Показываем элемент
    	}	
    	else obj.style.display = "none"; //Скрываем элемент
    }
}   

const RegistrationMenu = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="startMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}> Laundry </PanelHeader>
		<Div className = "MainReg">
			<RenderNotify />
			<Div className = "TextBlockReg">
				Введите ваш регистрационный код
			</Div>
			<Div className = "InputReg">
				<Input background-color="#FFF" type ="text"  id="input-id" >
				</Input>
			</Div>
			<Div className = "ImgPosition"> 
				<img className = "ImgCodeRegistration" src={Coderegistration}></img>
			</Div>
			<Div className ="ButtonBlockReg"> 
				<Button className = "ButtonReg" id ="Hide" size="xl" mode="outline" onClick={props.go} data-to="mainMenu">
					<Div className ="FontButtonReg">ПОТВЕРДИТЬ</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

RegistrationMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RegistrationMenu;

