import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';

import CodeView from '../img/CodeView.png';
import CodeViewScreen from '../img/CodeViewScreen.png';
import './CodeMenuStyle.css';

const osName = platform();

const CodeMenu = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="mainMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}>Laundry</PanelHeader>
		<Div className="MainCode">
			<Div className="TextBlockCode">
				Поднесите код к считывателю для потверждения
			</Div>
			<Div className = "QRCode">
				<img className = "ImgCodeViewScreen" src ={CodeViewScreen}></img>
			</Div>
			<Div className = "ButtonBlockCodeAlign">
				<Button className="ButtonBlcokCode" size="xl" mode="outline" onClick={props.go} data-to="mainMenu">
					<Div className = "FontButtonCode">НАЗАД</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

CodeMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default CodeMenu ;
